import { map } from 'rxjs/operators';
import { AuthData } from './../models/auth-data';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment'


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private token: any;
  private authStatus = new Subject<boolean>();
  private isAuthenticated = false;
  BACKEND_URL = environment.apiUrl;
  tokenTimeout: any;
  user: any;
  constructor(
    private http: HttpClient,
    private router: Router
  ) { }


  getToken() {
    return this.token;
  }

  getAuthStatus() {
    return this.authStatus.asObservable();
  }

  getIsAuth() {
    return this.isAuthenticated;
  }

  createUser(firstName: string, lastName: string, gender: string, ages: number, email: string, password: string) {
    const authData: AuthData = {
      firstName: firstName,
      lastName: lastName,
      gender: gender,
      ages: ages,
      email: email,
      password: password
    }

    this.http.post<{ token: string, }>(this.BACKEND_URL + "/users", authData)
      .subscribe(response => {
        this.router.navigate(["/login"]);
      })
  }

  login(email: string, password: string) {
    const authData = { email: email, password: password };

    this.http.post<{ token: string, expiresIn: number }>(this.BACKEND_URL + "/login", authData)
      .subscribe(response => {
        const token = response.token;
        this.token = token;
        if (token) {
          const expiresIn = response.expiresIn;
          this.setAuthTimer(expiresIn);
          this.isAuthenticated = true;
          this.authStatus.next(true)
          const now = new Date();
          const expirationDate = new Date(now.getTime() + expiresIn * 1000);
          this.saveAuthData(token, expirationDate);
          this.router.navigate(['/']);
        }

      })
  }

  getProfile() {
    return this.http.get<{ user: any }>(this.BACKEND_URL + '/users/me')
  }

  uploadProfilePicture(pic: FormData) {
    return this.http.post(this.BACKEND_URL + '/users/me/image', pic)
      .subscribe(res => {});

  }

  autoAuthUser() {
    const authInformation = this.getAuthData();
    if (!authInformation) {
      return;
    }
    const now = new Date();
    const expiresIn = authInformation.expirationDate.getTime() - now.getTime();
    if (expiresIn > 0) {
      this.token = authInformation.token;
      this.isAuthenticated = true;
      this.setAuthTimer(expiresIn / 1000);
      this.authStatus.next(true);
    }
  }

  logout() {
    this.token = null;
    this.isAuthenticated = false;
    this.authStatus.next(false);
    clearTimeout(this.tokenTimeout);
    this.clearAuthData();
    this.router.navigate(['/']);
  }

  private setAuthTimer(duration: number) {
    this.tokenTimeout = setTimeout(() => {
      this.logout();
    }, duration * 1000);
  }


  private saveAuthData(token: string, expirationDate: Date) {
    localStorage.setItem('token', token);
    localStorage.setItem('expiration', expirationDate.toISOString());
  }

  private clearAuthData() {
    localStorage.removeItem('token');
    localStorage.removeItem('expiration');
  }

  private getAuthData() {
    const token = localStorage.getItem('token');
    const expirationDate = localStorage.getItem('expiration');

    if (!token || !expirationDate) {
      return;
    }

    return {
      token,
      expirationDate: new Date(expirationDate),
    };
  }
}
