import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import * as mapboxgl from 'mapbox-gl'
import { Feature } from './../models/feature.model';
import { map } from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class MapService {

  features: Feature[] = [];
  map: mapboxgl.Map;
  style = 'mapbox://styles/mapbox/streets-v11';
  lat = 42.000721;
  lng = 21.425545;
  zoom = 12;

  geojson = {
    type: 'FeatureCollection',
    features:
      [{
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates: [21.425545, 42.000721]
        },
        properties: {
          title: 'Mapbox',
          description: 'Washington, D.C.'
        }
      },
      {
        type: 'Feature',
        geometry: {
          type: 'Point',
          coordinates: [21.4308192, 41.9951536]
        },
        properties: {
          title: 'Mapbox',
          description: 'Washington, D.C.'
        }
      }]
  };


  constructor(
    private http: HttpClient
  ) {
    this.map = mapboxgl.Map as any;
    (mapboxgl as any).accessToken = environment.mapbox.accessToken;
  }

  async buildMap() {
    this.map = new mapboxgl.Map({
      container: 'map',
      style: this.style,
      zoom: this.zoom,
      center: [this.lng, this.lat],
    })
    this.map.addControl(new mapboxgl.NavigationControl());
    this.features = (await this.getGeoJSON()).data
    this.addMarker()
  }


  getGeoJSON() {
    return this.http.get<{ data: Feature[] }>('http://localhost:3000/points')
      .pipe(
        map(points => {
          return {
            data: points.data.map((point: any) => {
              return {
                type: 'Feature',
                geometry: {
                  type: 'Point',
                  coordinates: [point.location.coordinates[0], point.location.coordinates[1]]
                },
                properties: {
                  title: 'Mapbox',
                  description: 'Washington, D.C.'
                }
              }
            })
          }
        })
      ).toPromise()
  }

  addMarker() {
    const geoJson = {
      type: 'FeatureCollection',
      features: this.features
    }

    geoJson.features.forEach((marker) => {
      new mapboxgl.Marker({})
        .setLngLat([marker.geometry.coordinates[1], marker.geometry.coordinates[0]])
        .addTo(this.map);
    })
  }
}
